import { Injectable } from "../../../node_modules/@angular/core";
import { AngularFireDatabase } from "../../../node_modules/angularfire2/database";

@Injectable()
export class RatingService {
    private PATH = "/lecture/";
    constructor(public db: AngularFireDatabase) {

    }

    sendLectureRating(value: number, lectureId: number) {
        this.db.database.ref(this.PATH + lectureId).once("value", (snapshot) => {
            let total;
            let amount;
            let average;
            //Puxa valores
            total = snapshot.child("rating").child("lecture").child("value").val();
            amount = snapshot.child("rating").child("lecture").child("amount").val();

            //Aplicando a logica
            total += value;
            amount++;
            average = total / amount;
            average = average.toFixed(1);
            //Salvando os valores
            this.db.database.ref(this.PATH + lectureId + "/rating/lecture").set({
                value: total,
                amount: amount,
                average: average
            })
        })
    }

    sendSpeakerRating(value: number, lectureId: number) {
        this.db.database.ref(this.PATH + lectureId).once("value", (snapshot) => {
            let total;
            let amount;
            let average;
            //Puxa valores
            total = snapshot.child("rating").child("speaker").child("value").val();
            amount = snapshot.child("rating").child("speaker").child("amount").val();

            //Aplicando a logica
            total += value;
            amount++;
            average = total / amount;
            average = average.toFixed(1);
            //Salvando os valores
            this.db.database.ref(this.PATH + lectureId + "/rating/speaker").set({
                value: total,
                amount: amount,
                average: average
            })
        })

    }
        //Static
    getLectureRating(lectureId: number) {
        return new Promise<number>((resolve, reject) => {
            this.db.database.ref(this.PATH + lectureId + "/rating/lecture/").on("value", snapshot => {
                return resolve(snapshot.child("average").val());
            })
        }).then(value2 => {
            return value2;
        })
    }
    
    getSpeakerRating(lectureId: number) {
        return new Promise<number>((resolve, reject) => {
            this.db.database.ref(this.PATH + lectureId + "/rating/speaker/").on("value", snapshot => {
                return resolve(snapshot.child("average").val());
            })
        }).then(value => {
            return value;
        })
    }
        //Realtime
    getLectureRatingObservable(lectureId: number){
        return this.db.object(this.PATH + lectureId + "/rating/lecture/average").valueChanges();
    }

    etSpeakerRatingObservable(lectureId: number){
        return this.db.object(this.PATH + lectureId + "/rating/speaker/average").valueChanges();
    }
}


