import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HomePage } from '../home/home';
import { AlertController } from 'ionic-angular';
import { ParameterHelperService } from './../../providers/parameter-helper/parameter-helper.service';


/**
 * Generated class for the PerfilPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-perfil',
  templateUrl: 'perfil.html',
})
export class PerfilPage {


  user = {
    "nome": "--",
    "telefone": "--",
    "empresa": "--",
    "email": "--",
  }
  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController, public parameterHelperService: ParameterHelperService) { }


  showPrompt() {
    const prompt = this.alertCtrl.create({
      title: 'Atualize seu perfil',
      message: "Aqui você pode atualizar alguns campos do seu perfil.",
      inputs: [
        {
          name: 'telefone',
          placeholder: 'Celular'
        },
        {
          name: 'empresa',
          placeholder: 'Empresa'
        },
        {
          name: 'ocupacao',
          placeholder: 'Ocupação, cargo, etc...'
        },
      ],
      buttons: [
        {
          text: 'Cancelar',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Salvar',
          handler: data => {
            console.log('Saved clicked');
          }
        }
      ]
    });
    prompt.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PerfilPage');
    this.parameterHelperService.getParameter("user").then(use =>{
      this.user = use;
    });
  }

  returnLogin() {
    this.navCtrl.setRoot(HomePage);
  }

}



