import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { NewsEditPage } from '../news-edit/news-edit';
import { ComponentHelperService } from '../../providers/component-helper/component-helper.service';
import { NewsPage } from '../news/news';
import { NewsService } from '../../providers/news-service/news.service';
import { ParameterHelperService } from '../../providers/parameter-helper/parameter-helper.service';

/**
 * Generated class for the DetailNoticePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-detail-notice',
  templateUrl: 'detail-notice.html',
})
export class DetailNoticePage {

  public esconder:boolean = false;
  notice: any;
  public loader;
  openLoading() {
    this.loader = this.loadingCtrl.create({
    });
    this.loader.present();
  }
  closeLoading() {
    this.loader.dismiss();
  }
  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public componentHelperService: ComponentHelperService, public service: NewsService, public parameterHelperService: ParameterHelperService) {
    this.openLoading();
    this.notice = navParams.data.notice || {};
    this.closeLoading();

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetailNoticePage');
    this.typeUser();
  }

  goNewsEdit(notice: any) {
    this.navCtrl.push(NewsEditPage, { notice: notice });
  }

  noticeRemove(key: string) {
    if (key) {
      this.service.RemoveNew(key);
    }
    this.navCtrl.setRoot(NewsPage);
    this.componentHelperService.presentToast("Notícia removida com sucesso!");
  }

  typeUser() {
    var user = this.parameterHelperService.getParameter("user");
    user.then(use => {
      console.log(use);
      console.log(use.perfis.length);
      

      if(use.perfis.length == 1){
        this.esconder = false;
      }else{
        this.esconder = true;
      }
    });
  }
}



