import { Injectable } from "../../../node_modules/@angular/core";
import { AngularFireDatabase } from '../../../node_modules/angularfire2/database';
import { AngularFireStorage } from '../../../node_modules/angularfire2/storage';
import { map } from '../../../node_modules/rxjs/operators';

@Injectable()
export class NewsService {
    private PATH = "news/";
    constructor(public db: AngularFireDatabase, public storage: AngularFireStorage) {

    }

    GetFirstNews() {
        return this.db.list(this.PATH, ref => ref.orderByChild("timestamp").limitToFirst(1)).snapshotChanges().pipe(
            map(changes => {
                return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }))
            })
        )
    }

    GetAllNews() {
        return this.db.list(this.PATH, ref => ref.orderByChild("timestamp")).snapshotChanges().pipe(
            map(changes => {
                return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }))
            })
        )
    }

    SaveNew(news: any) {
        if (news.key) {
            this.db.list(this.PATH).update(news.key, news)
        } else {
            this.db.list(this.PATH).push(news);
        }
    }

    RemoveNew(key: string) {
        return this.db.list(this.PATH).remove(key);
    }
}


