import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
import { ComponentHelperService } from '../../providers/component-helper/component-helper.service';
import { RatingService } from '../../providers/rating-service/rating.serive';


/**
 * Generated class for the RatingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-rating',
  templateUrl: 'rating.html',
  providers: [
    EventProvider
  ]
})
export class RatingPage {
  public evento;
  public eventoid;
  public loader;
  public valueRatingLecture = 1; // Valor padrão
  public valueRatingSpeaker = 1; // Valor padrão  
  public value: any;
  public value2: any;


  public eventos = new Array<any>();

  constructor(public navCtrl: NavController, 
    public navParams: NavParams, 
    public componentHelperService: ComponentHelperService, 
    public eventProvider: EventProvider, 
    public loadingCtrl: LoadingController,
    public ratingService: RatingService
    ){

  }

  log(valor){
    console.log(valor);
    //Setar os Valores do rating do palestrando e da palestra
  }

  setRatingLecturevalue(value){
    this.valueRatingLecture = value;
  }

  setRatingSpeakervalue(value){
    this.valueRatingSpeaker = value;
  }

  openLoading() {
    this.loader = this.loadingCtrl.create({
    });
    this.loader.present();
  }
  closeLoading() {
    this.loader.dismiss();
  }

  ionViewDidEnter() {
    this.openLoading();
    this.eventoid = this.navParams.get("id");
    this.eventProvider.getEventId(this.eventoid).subscribe(data => {
      let retorno = (data as any)._body;
      this.evento = JSON.parse(retorno);
      console.log(this.evento);
      this.closeLoading();
    }, error => {
      console.log(error);
      this.closeLoading();
    }
    )    
    console.log("Evento id:", this.eventoid)
  }

  sendRating(){
    this.ratingService.sendLectureRating(this.valueRatingLecture, this.eventoid);
    this.ratingService.sendSpeakerRating(this.valueRatingSpeaker, this.eventoid);
    this.navCtrl.pop();
    this.componentHelperService.presentToast("Avaliação enviada com sucesso! Obrigado pela participação.");

  }

}



