import { NewsService } from './../../providers/news-service/news.service';
import { Component } from '@angular/core';
import { NavController, MenuController, LoadingController } from 'ionic-angular';

import { ComponentHelperService } from '../../providers/component-helper/component-helper.service';

import { ParameterHelperService } from '../../providers/parameter-helper/parameter-helper.service';
import { NewsPage } from '../news/news';
import { Observable } from '../../../node_modules/rxjs';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  news: Observable<any[]>
  public loader;

  constructor(public navCtrl: NavController, public menuCtrl: MenuController, public componentHelperService: ComponentHelperService, public parameterHelperService: ParameterHelperService, public newsService: NewsService, public loadingCtrl: LoadingController) {
    this.menuCtrl.swipeEnable(true);
  }

  openLoading() {
    this.loader = this.loadingCtrl.create({
    });
    this.loader.present();
  }
  closeLoading() {
    this.loader.dismiss();
  }
  
  ionViewDidLoad(){
    this.openLoading();
    this.news = this.newsService.GetFirstNews();
    this.closeLoading();
  }

  moreNotice(){
    this.navCtrl.setRoot(NewsPage);
  }

  showToast() {
    this.componentHelperService.presentToast("teste 1001");
  }

  showLoading() {
    let loading = this.componentHelperService.presentLoading();
    setTimeout(function(){ 
      loading.dismiss();
    }, 3000);
  }

  showAlert() {
    this.componentHelperService.presentAlert("Teste Title", "Teste Subt");
  }

}



