import { Injectable } from "../../../node_modules/@angular/core";
import { AngularFireDatabase } from "../../../node_modules/angularfire2/database";
import { map } from "../../../node_modules/rxjs/operators";

@Injectable()
export class QuestionService {
    private PATH = "lecture/";
    constructor(public db: AngularFireDatabase){

    }


    GetAllQuestion(lectureID: string){
        return this.db.list(this.PATH + lectureID + "/message", ref => ref.orderByChild("timestamp")).snapshotChanges().pipe(
            map(changes => {
                return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }))
            })
        )
    }

    NewQuestion(question: any, lectureID: string){
        this.db.list(this.PATH + lectureID + "/message").push(question);
    }

}


