import { Component, Input, Output, EventEmitter } from '@angular/core';



@Component({
  selector: 'ion-rating',
  templateUrl: 'ion-rating.html'
})
export class IonRatingComponent {

  @Input() numStar: number = 5;
  @Input() value: number = 3;
  @Input() leitura: boolean = false;

  @Output() starClick: EventEmitter<number> = new EventEmitter<number>();

  stars: string[] = [];

  constructor() {
    this.calc();
  }

  ngAfterViewInit(){
    
  }

  calc(){
    this.stars = [];
    let tmp = this.value;
    for(let i=0; i<this.numStar; i++, tmp--){
      if(tmp>=1)
        this.stars.push("star");
      else if(tmp > 0 && tmp < 1)
        this.stars.push("star-half");
      else this.stars.push("star-outline");
    }
  }

  starClicked(index){
    if(!this.leitura){
      this.value = index+1;
      this.starClick.emit(this.value);
      this.calc();
    }
  }

}



