import { ParameterHelperService } from './../../providers/parameter-helper/parameter-helper.service';
import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { DetailProgrammingPage } from '../detail-programming/detail-programming';
import { ComponentHelperService } from '../../providers/component-helper/component-helper.service';
import { EventProvider } from '../../providers/event/event';
import { LocalNotifications, ILocalNotification } from '@ionic-native/local-notifications';

@Component({
  selector: 'page-programming',
  templateUrl: 'programming.html',
  providers: [
    EventProvider
  ]
})
export class ProgrammingPage {
  //Iniciar a page na tela Agenda do Evento
  programmings: string = "agenda_evento";
  days: string = "day1";
  my_days: string = "my_day1";
  order: string = 'id';
  public events: any;
  public loader;
  public esconde19: boolean = false;

  my_lista_eventos = new Array<any>();


  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public componentHelperService: ComponentHelperService,
    private eventProvider: EventProvider,
    public loadingCtrl: LoadingController,
    private localNotifications: LocalNotifications,
    public parameterHelperService: ParameterHelperService
  ) {

  }

  escondeDia19(date) {
    if (this.convertDay(date) == '19') {
      this.esconde19 = true;
    }
  }

  public lista_eventos = new Array<any>();
  public lista_eventos2 = new Array<any>();

  openLoading() {
    this.loader = this.loadingCtrl.create({
    });
    this.loader.present();
  }
  closeLoading() {
    this.loader.dismiss();
  }

  ionViewDidEnter() {
    this.parameterHelperService.getParameter("my_lista_eventos")
      .then((data: any) => {
        if (data) {
          this.my_lista_eventos = data;
        }
      })
    this.openLoading();
    this.eventProvider.getAllEvent().subscribe(
      data => {
        const response = (data as any);
        const objeto_retorno = JSON.parse(response._body);
        this.lista_eventos = objeto_retorno;
        console.log(objeto_retorno);
        this.closeLoading();
      }, error => {
        console.error(error);
        this.closeLoading();
      }
    )
  }

  convertDate(date) {
    let dateArray = date;
    let newDate = dateArray[11] + dateArray[12] + ':' + dateArray[14] + dateArray[15];
    return newDate;
  }

  convertDay(date) {
    let dateArray = date;
    let newDate = dateArray[0] + dateArray[1];
    return newDate;
  }

  //Vai para página de detalhes enviando o ID
  goDetails(evento) {
    this.navCtrl.push(DetailProgrammingPage, { id: evento.id });
  }

  removeEvent(evento) {
    let i = 0;
    this.my_lista_eventos.forEach(myEvento => {
      if (myEvento.id == evento.id) {
        this.my_lista_eventos.splice(i, 1);
        this.localNotifications.clear(evento.id);
      }
      i++;
    });
    this.parameterHelperService.setParameter("my_lista_eventos", this.my_lista_eventos);
  }

  eventAddMessage(evento) {
    let flag = false;
    this.my_lista_eventos.forEach(myEvento => {
      if (myEvento.id == evento.id) {
        flag = true;
      }
    });
    if (!flag) {
      this.my_lista_eventos.push(evento);
      this.parameterHelperService.setParameter("my_lista_eventos", this.my_lista_eventos);
      let notifications: Array<ILocalNotification> = [];
      this.localNotifications.getAll()
        .then((data: any) => {
          notifications = data;

          let date = new Date(evento.datainicio.split("/")[1] + "/" + evento.datainicio.split("/")[0] + "/" + evento.datainicio.split("/")[2]);

          if (notifications.length > 0) {
            this.localNotifications.cancelAll();
            notifications.push({ id: evento.id, text: evento.titulo + ' vai começar em 10 minutos', trigger: { at: new Date(date.getTime() - 60000 * 10) } });
            this.localNotifications.schedule(notifications);
          } else {
            this.localNotifications.schedule([{
              id: evento.id, text: evento.titulo + ' vai começar em 10 minutos', trigger: { at: new Date(date.getTime() - 60000 * 10) }
            }]);
          }
          this.componentHelperService.presentToast("Adicionado na agenda com sucesso!");
        })
    } else {
      this.componentHelperService.presentToast("Evento já está na sua agenda!");
    }

  }


}



