import { ComponentHelperService } from './../../providers/component-helper/component-helper.service';
import { EventProvider } from './../../providers/event/event';
import { ParameterHelperService } from './../../providers/parameter-helper/parameter-helper.service';
import { Component } from '@angular/core';
import { NavController, NavParams, MenuController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { RegisterPage } from '../register/register';
import { ForgotPassPage } from '../forgot-pass/forgot-pass';
import { ProgrammingPage } from '../programming/programming';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  user: { email: string, password: string } = {
		email: null,
		password: null
	};


  constructor(public navCtrl: NavController, public navParams: NavParams, public menuCtrl: MenuController,
    public parameterHelperService: ParameterHelperService, public eventProvider: EventProvider, public componentHelperService: ComponentHelperService) {
    this.menuCtrl.swipeEnable(false);
  }

  ionViewDidLoad() {
    
  }

  login() {
    let loading = this.componentHelperService.presentLoading();
    this.eventProvider.login(this.user)
    .then((data: any) => {
      let user: any = this.user;
      if (user.email == "emanuelcmesquita@gmail.com" || user.email == "andrezaadrifer@gmail.com" || user.email == "marcmatiasdev@gmail.com" || user.email == "victor-21-@hotmail.com" || user.email == "gabriellopesveras@gmail.com" || user.email == "jordandossantosoliveira@gmail.com" || user.email == "ytalowilker97@gmail.com" || user.email == "eventos@abrhpb.com.br") {
        user.type = "adminUser";
      } else {
        user.type = "simpleUser";
      }
      console.log(data);
      this.eventProvider.getUser(data.headers.get("id_user")).then((value: any) =>{
        let userFull = JSON.parse(value._body);
        this.parameterHelperService.setParameter("user", userFull);
      });

      loading.dismiss();
      this.navCtrl.setRoot(ProgrammingPage);
    }).catch((err: any) => {
      if (err && err.bodyJson) {
        this.componentHelperService.presentToast(err.bodyJson.message);
      } else {
        this.componentHelperService.presentToast("Falha na Operação");
      }
      loading.dismiss();
    });
  }

  forgotPass() {
    this.navCtrl.push(ForgotPassPage);
  }

  register() {
    this.navCtrl.push(RegisterPage);
  }

}



