import { DetailNoticePage } from './../detail-notice/detail-notice';
import { NewsEditPage } from './../news-edit/news-edit';
import { NewsService } from './../../providers/news-service/news.service';
import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { Observable } from '../../../node_modules/rxjs';
import { ParameterHelperService } from '../../providers/parameter-helper/parameter-helper.service';

@Component({
  selector: 'page-news',
  templateUrl: 'news.html',
})
export class NewsPage {

  public esconder:boolean = false;
  news: Observable<any[]>;
  public loader;
  constructor(public navCtrl: NavController, public navParams: NavParams, public newsService: NewsService, public loadingCtrl: LoadingController, public parameterHelperService: ParameterHelperService) {
    
  }

  openLoading() {
    this.loader = this.loadingCtrl.create({
    });
    this.loader.present();
  }
  closeLoading() {
    this.loader.dismiss();
  }

  ionViewDidLoad() {
    this.openLoading();
    console.log('ionViewDidLoad NewsPage');
    this.news = this.newsService.GetAllNews();
    this.closeLoading();
    this.typeUser();
  }

  goDetailNotice(notice : any){
    this.navCtrl.push(DetailNoticePage, { notice: notice});
  }

  goNewsEdit(){
    this.navCtrl.push(NewsEditPage);
  }

  typeUser() {
    var user = this.parameterHelperService.getParameter("user");
    user.then(use => {
      console.log(use);
      console.log(use.perfis.length);
      

      if(use.perfis.length == 1){
        this.esconder = false;
      }else{
        this.esconder = true;
      }
    });
  }
}
  



