import { ParameterHelperService } from './../../providers/parameter-helper/parameter-helper.service';
import { QuestionService } from './../../providers/question-service/question.service';
import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { ComponentHelperService } from '../../providers/component-helper/component-helper.service';
import { EventProvider } from '../../providers/event/event';
import { RatingPage } from '../rating/rating';
import { RatingService } from '../../providers/rating-service/rating.serive';
import { Observable } from 'rxjs';
import { LocalNotifications, ILocalNotification } from '@ionic-native/local-notifications';

/**
 * Generated class for the DetailProgrammingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-detail-programming',
    templateUrl: 'detail-programming.html',
    providers: [
        EventProvider
    ]
})
export class DetailProgrammingPage {
    public esconder:boolean = false;
    public evento;
    public eventoid;
    public loader;
    public valueRatingLecture: Observable<any>; // Valor padrão
    public valueRatingSpeaker: Observable<any>; // Valor padrão 
    questionList: Observable<any[]>;
    hideMe: boolean;
    esconderBotao: boolean = true;
    public user: any;

    my_lista_eventos = new Array<any>();

    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public componentHelperService: ComponentHelperService,
        public eventProvider: EventProvider,
        public loadingCtrl: LoadingController,
        public alertCtrl: AlertController,
        public ratingService: RatingService,
        public questionService: QuestionService,
        private localNotifications: LocalNotifications,
        public parameterHelperService: ParameterHelperService) {
    }
    hide() {
        this.hideMe = true;
        if (this.hideMe == true) {
            this.esconderBotao = false;
        }
    }

    hideOff() {
        this.hideMe = false;
        if (this.hideMe == false) {
            this.esconderBotao = true;
        }
    }



    convertDate(date) {
        let dateArray = date;
        let newDate = dateArray[11] + dateArray[12] + ':' + dateArray[14] + dateArray[15];
        return newDate;
    }

    openLoading() {
        this.loader = this.loadingCtrl.create({
        });
        this.loader.present();
    }
    closeLoading() {
        this.loader.dismiss();
    }

    ionViewDidEnter() {
        this.parameterHelperService.getParameter("my_lista_eventos")
        .then((data: any) => {
          if (data) {
            this.my_lista_eventos = data;
          }
        })

      this.parameterHelperService.getParameter("user").then(user => {
        this.user = user;
      })

        this.openLoading();
        this.eventoid = this.navParams.get("id");
        this.eventProvider.getEventId(this.eventoid).subscribe(data => {
            let retorno = (data as any)._body;
            this.evento = JSON.parse(retorno);
            this.closeLoading();
        }, error => {
            console.log(error);
            this.closeLoading();
        }
        )
        //Exemplo de pegar a media da avaliação
        this.valueRatingLecture = this.ratingService.getLectureRatingObservable(this.eventoid);

        this.valueRatingSpeaker = this.ratingService.etSpeakerRatingObservable(this.eventoid);

        this.questionList = this.questionService.GetAllQuestion(this.eventoid);

        console.log("Evento id:", this.eventoid)
        this.typeUser();
    }
    removeEvent(evento) {
        let i = 0;
        this.my_lista_eventos.forEach(myEvento => {
          if (myEvento.id == evento.id) {
            this.my_lista_eventos.splice(i, 1);
            this.localNotifications.clear(evento.id);
          }
          i++;
        });
        this.parameterHelperService.setParameter("my_lista_eventos", this.my_lista_eventos);
      }


    eventAddMessage(evento) {
        let flag = false;
        this.my_lista_eventos.forEach(myEvento => {
          if (myEvento.id == evento.id) {
            flag = true;
          }
        });
        if (!flag) {
          this.my_lista_eventos.push(evento);
          this.parameterHelperService.setParameter("my_lista_eventos", this.my_lista_eventos);
          let notifications: Array<ILocalNotification> = [];
          this.localNotifications.getAll()
            .then((data: any) => {
              notifications = data;
    
              let date = new Date(evento.datainicio.split("/")[1] + "/" + evento.datainicio.split("/")[0] + "/" + evento.datainicio.split("/")[2]);
    
              if (notifications.length > 0) {
                this.localNotifications.cancelAll();
                notifications.push({ id: evento.id, text: evento.titulo + ' vai começar em 10 minutos', trigger: { at: new Date(date.getTime() - 60000 * 10) } });
                this.localNotifications.schedule(notifications);
              } else {
                this.localNotifications.schedule([{
                  id: evento.id, text: evento.titulo + ' vai começar em 10 minutos', trigger: { at: new Date(date.getTime() - 60000 * 10) }
                }]);
              }
              this.componentHelperService.presentToast("Adicionado na agenda com sucesso!");
            })
        } else {
          this.componentHelperService.presentToast("Evento já está na sua agenda!");
        }
    
      }

    alertComment() { ; debugger
        let prompt = this.alertCtrl.create({
            title: 'PERGUNTAR',
            inputs: [
                {
                    type: 'textarea',
                    name: 'perguntar',
                    placeholder: 'Perguntar'
                }
            ],
            buttons: [
                {
                    text: "Cancelar",
                    handler: data => {
                        console.log("Cancelado");
                    }
                },
                {
                    text: "Enviar",
                    handler: data => {
                        if(data.perguntar != ""){
                            var date = new Date();
                            var question = {
                                content: data.perguntar,
                                name: this.user.nome,
                                timestamp: -(Date.now()),
                                datetime: (date.getDate().toString() + "/" + (date.getMonth() + 1).toString() + "/" + date.getFullYear().toString() + " - " + date.getHours().toString() + ":" + date.getMinutes().toString())
                            }
                            this.questionService.NewQuestion(question, this.eventoid);
                            this.componentHelperService.presentToast("Pergunta enviada com sucesso! Obrigado pela participação.");
                            console.log("Enviado");
                        } else {
                            this.componentHelperService.presentToast("Campo vazio");
                            console.log("Campo vazio");
                        }
                    }
                }
            ]
        });
        prompt.present();
    }

    openRating(evento) {
        this.navCtrl.push(RatingPage, { id: evento.id });
    }

    typeUser() {
        var user = this.parameterHelperService.getParameter("user");
        user.then(use => {
          console.log(use);
          console.log(use.perfis.length);
          
    
          if(use.perfis.length == 1){
            this.esconder = false;
          }else{
            this.esconder = true;
          }
        });
      }
}



