import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { ComponentHelperService } from '../../providers/component-helper/component-helper.service';
import { EventProvider } from '../../providers/event/event';

@Component({
  selector: 'page-forgot-pass',
  templateUrl: 'forgot-pass.html',
  providers: [
    EventProvider
  ]
})
export class ForgotPassPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public componentHelperService: ComponentHelperService, public eventProvider: EventProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ForgotPassPage');
  }
  backLogin(){
    this.navCtrl.setRoot(LoginPage);
  }

  //Enviar email
  sendPass(email){
    let p = this.eventProvider.callEmailPost(email);
    p.then(data =>{
      console.log("Recebido" + JSON.stringify(data.json()));
      this.componentHelperService.presentToast("Nova senha enviada. Por favor, verifique o email.");
    }, error => {
      console.error(error);
      this.componentHelperService.presentToast("Email inválido.");
    }
    )
    
    
  }
}



