import { ParameterHelperService } from './../providers/parameter-helper/parameter-helper.service';
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { ProgrammingPage } from '../pages/programming/programming';
import { NewsPage } from '../pages/news/news';
import { LoginPage } from '../pages/login/login';
import { SponsorsPage } from '../pages/sponsors/sponsors';
import { AboutEventPage } from '../pages/about-event/about-event';
import { PerfilPage } from '../pages/perfil/perfil';
import { InformationPage } from '../pages/information/information';
import { MapsPage } from '../pages/maps/maps';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any;

  pages: Array<{title: string, component: any, icon: string}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public parameterHelperService: ParameterHelperService) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Home', component: HomePage, icon: 'md-home' },
      { title: 'Programação', component: ProgrammingPage, icon: 'md-calendar'},
      { title: 'Notícias', component: NewsPage, icon: 'md-paper'},
      { title: 'Sobre o Evento', component: AboutEventPage, icon: 'md-information'},
      { title: 'Informações Gerais', component: InformationPage, icon: 'md-information-circle'},
      { title: 'Parceiros', component: SponsorsPage, icon: 'md-people'},
      { title: 'Meu Perfil', component: PerfilPage, icon: 'md-person'},
      { title: 'Local', component: MapsPage, icon: 'md-map'},
      { title: 'Sair', component: LoginPage, icon: 'md-exit'}
    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.userLoggedIn();
      this.splashScreen.hide();
    });
  }

  userLoggedIn() {
    this.parameterHelperService.getParameter("user")
    .then((data: any) => {
      if (data && data.email) {
        this.rootPage = ProgrammingPage;
      } else {
        this.rootPage = LoginPage;
      }
    })
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    if (page.title == "Sair") {
      this.parameterHelperService.setParameter("user", null);
    }
    this.nav.setRoot(page.component);
  }
}



